export default {
    props : {
        postId : {
            // type: String,
            required : true,
        },
    },
    data(){
        return {
            valid: false,
            content : '',
            success: false, 
            successMessages : '',
            hideDetails: true
        }
    },
    computed : {
        me() {
            return this.$store.state.users.me;

        },
    },
    methods : {
        onChangeTextarea(value){
            if(value.length){
                this.hideDetails=false;
                this.success = false;
                this.successMessages = '';
            }
        },
        onSubmitForm(){
            // console.log(this.me.nickName);
            // return false;
            if(this.$refs.form.validate()){
                this.$store.dispatch('posts/addComment',{
                    postId: this.postId,
                    content : this.content,
                
                }).then(()=>{
                    this.content='';
                    this.success = true;
                    this.successMessages= ' ';
                    this.hideDetails = false;
                }).catch(()=>{

                });
            }
        }
    }
}

